<template>
	<div>

		<el-button size="mini" class="add" @click="updataTimeSlotDialog = true; isAdd = true"></el-button>

		<el-table :data="timeSlotData" style="width: 100%">
			<el-table-column prop="slotId" label="id">
			</el-table-column>
			<el-table-column prop="slotTime" label="">
			</el-table-column>
			<el-table-column align="center" label="" width="160">
				<template slot-scope="scope">
					<el-button size="mini" @click="showUpdate(scope.$index)"></el-button>
					<el-button size="mini" type="danger" @click="handleDelete(scope.$index, timeSlotData)"></el-button>
					<el-dialog title="" :visible.sync="updataTimeSlotDialog">
						<el-form>
							<el-time-select placeholder="" v-model="startTime" :picker-options="{
      start: '08:00',
      step: '00:30',
      end: '23:00'
    }">
							</el-time-select>
							<el-time-select placeholder="" v-model="endTime" :picker-options="{
      start: '08:00',
      step: '00:30',
      end: '23:00',
      minTime: startTime
    }">
							</el-time-select>
						</el-form>
						<div slot="footer" class="dialog-footer">
							<el-button @click="updataTimeSlotDialog = false"> </el-button>
							<el-button v-if="isAdd" type="primary" @click="addTimeSlot(selectIndex, timeSlotData)"> </el-button>
							<el-button v-else type="primary" @click="handleUpdate(selectIndex, timeSlotData)"> </el-button>
						</div>
					</el-dialog>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
	import axios from 'axios'
	import $ from 'jquery'

	export default {
		name: 'ManageTimeSlot',
		data() {
			return {
				isAdd: false,
				selectIndex: 0,
				timeSlotData: [],
				activityList: [],
				userProtocol: '',
				startTime: '',
				endTime: '',
				updataTimeSlotDialog: false,
				form: {
					title: '',
					pubHouse: '',
					description: '',
					image: '11'
				},

				fileList: [],
			}
		},
		methods: {
			test(index, row) {
				console.log(index)
				console.log(row[index].slotId)
			},
			onChange(file, fileList) {
				console.log(document.querySelector("input[type=file]").files);
				console.log(fileList);
				//	this.form.image = file.url;
				//this.setImagePreview();
			},
			beforeUpload(file) {
				console.log(file)
			},
			// templatedialogscope.$indexindex
			showUpdate(index) {
				this.selectIndex = index;
				this.updataTimeSlotDialog = true;
			},
			addTimeSlot() {
				this.isAdd = false;
				var _this = this;
				var params = new URLSearchParams();
				this.updataTimeSlotDialog = false;
				params.append('slotTime', this.startTime + '-' + this.endTime);
				axios({
						method: 'post',
						dataType: 'json',
						url: '/apis/addTimeSlot.do',
						data: params,
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'x-key': window.sessionStorage.getItem('adminId'),
							'x-token': window.sessionStorage.getItem('token')
						},
					})
					.then(function(response) {
						console.log(response);
						if(response.data.statusCode == 102) {
							_this.getTimeSlots();
						} else {
							alert(response.data.message);
						}
					}).catch(function(err) {
						console.log(err);
						alert("");
					});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			handleRemove(file, fileList) {

			},
			handlePreview(file) {
				console.log(file);
			},
			uploadSuccess(res, files, fileList) {
				if(res.status == 4007) {
					this.$message.warning(res.msg + ",.png.jpg.jpeg");
				}
				console.log(res);
				//this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
			},
			uploadError(err, files, fileList) {
				this.$message.warning(",");
				console.log(err);
				console.log(fileList);
				//this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
			},

			timestampToTime(timestamp) {
				var myData = new Date(timestamp); //10*1000131000
				var Y = myData.getFullYear() + '-';
				var M = (myData.getMonth() + 1 < 10 ? '0' + (myData.getMonth() + 1) : myData.getMonth() + 1) + '-';
				var D = (myData.getDate() < 10 ? ('0' + myData.getDate()) : myData.getDate()) + ' ';
				var h = (myData.getHours() < 10 ? ('0' + myData.getHours()) : myData.getHours()) + ':';
				var m = (myData.getMinutes() < 10 ? ('0' + myData.getMinutes()) : myData.getMinutes()) + ':';
				var s = (myData.getSeconds() < 10 ? ('0' + myData.getSeconds()) : myData.getSeconds());

				return Y + M + D + h + m + s;
			},
			handleDelete(index, row) {
				var _this = this;
				console.log(row[index])
				var params = new URLSearchParams();
				params.append('slotId', row[index].slotId);
				axios({
						method: 'post',
						dataType: 'json',
						url: '/apis/deleteTimeSlot.do',
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'x-key': window.sessionStorage.getItem('adminId'),
							'x-token': window.sessionStorage.getItem('token')
						},
						data: params
					})
					.then(function(response) {
						if(response.data.statusCode == 102) {
							row.splice(index, 1);

						} else {
							alert(response.data.message);
						}
					}).catch(function(err) {
						alert("");
					});
			},
			getTimeSlots() {
				var _this = this;
				axios({
						method: 'get',
						dataType: 'json',
						url: '/apis/getTimeSlots.do',
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'x-key': window.sessionStorage.getItem('adminId'),
							'x-token': window.sessionStorage.getItem('token')
						},

					})
					.then(function(response) {
						_this.timeSlotData = [];
						if(response.data.statusCode == 102) {
							var responseData = response.data.result;
							for(var i = 0; i < responseData.length; i++) {
								_this.timeSlotData.push(responseData[i]);
							}

						} else {
							alert(response.data.message);
						}
					}).catch(function(err) {
						console.log(err)
						alert("");
					});
			},
			handleUpdate(index, row) {
				console.log(index);
				var _this = this;
				var params = new URLSearchParams();
				this.updataTimeSlotDialog = false;
				params.append('slotId', row[index].slotId);
				params.append('slotTime', this.startTime + '-' + this.endTime);
				axios({
						method: 'post',
						dataType: 'json',
						url: '/apis/updateTimeSlot.do',
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'x-key': window.sessionStorage.getItem('adminId'),
							'x-token': window.sessionStorage.getItem('token')
						},
						data: params
					})
					.then(function(response) {
						if(response.data.statusCode == 102) {
							_this.getTimeSlots();
						} else {
							alert(response.data.message);
						}
					}).catch(function(err) {
						alert("");
					});
			},
			handleAdd(index, row) {
				var _this = this;
				console.log(row[index])
				var params = new URLSearchParams();
				params.append('isbn', row[index].isbn);
				axios({
						method: 'post',
						dataType: 'json',
						url: '/apis/addBookById.do',
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'x-key': window.sessionStorage.getItem('adminId'),
							'x-token': window.sessionStorage.getItem('token')
						},
						data: params
					})
					.then(function(response) {
						if(response.data.statusCode == 102) {
							row[index].stock++;
						} else {
							alert(response.data.message);
						}
					}).catch(function(err) {
						alert("");
					});
			}
		},
		mounted() {
			this.getTimeSlots();
//			$.ajax({
//				type: "get",
//				//getpost
//				dataType: "json",
//				url: '/apis/getTimeSlots.do',
//				//
//				cache: false,
////				headers: {
////							'x-key': window.sessionStorage.getItem('adminId'),
////							'x-token': window.sessionStorage.getItem('token')
////				},
//				beforeSend: function(xhr) {
//					xhr.setRequestHeader('x-key', window.sessionStorage.getItem('adminId'));
//					xhr.setRequestHeader('x-token', window.sessionStorage.getItem('token'));
//				},
//				success: function(data) {
//					console.log(data);
//
//				},
//				error: function(err) {
//					console.log(err)
//				}
//			});
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}
	
	ul {
		list-style-type: none;
		padding: 0;
	}
	
	li {
		display: inline-block;
		margin: 0 10px;
	}
	
	a {
		color: #42b983;
	}
	
	.el-pagination {
		padding: 25px 5px;
		text-align: center;
	}
	
	.add {
		float: right;
		margin: 20px;
	}
</style>


